import React, { Fragment } from 'react';
import { Route, Switch } from 'react-router-dom';

import ManageContainer from './ManageContainer';
import MediaContainer from './MediaContainer';
import New from './NewContainer';
import NotFoundContainer from '../Exceptions/NotFoundContainer';
import ReviewContainer from './ReviewContainer';

const Products = ({ match }) => {
    return (
        <Fragment>
            <Switch>
                <Route exact path={`${match.url}/new`} component={New} />
                <Route exact path={`${match.url}/manage`} component={ManageContainer} />
                <Route exact path={`${match.url}/media-center`} component={MediaContainer} />
                <Route exact path={`${match.url}/reviews`} component={ReviewContainer} />

                <Route component={NotFoundContainer} />
            </Switch>

        </Fragment>
    );
};

export default Products;